.mt-de-history {
  min-width: 20em;
  text-align: center;
}

.mt-de-history-header {
  display: flex;
  justify-content: space-between;
}

.mt-de-history-list {
  list-style-type: none;
  padding: 0;
  max-height: 50vh;
  overflow-y: scroll;
}

.mt-de-history-list::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #f5f5f5;
}

.mt-de-history-list::-webkit-scrollbar {
  width: 6px;
  background-color: #f5f5f5;
}

.mt-de-history-list::-webkit-scrollbar-thumb {
  background-color: var(--secondary-color-1-0);
}

.mt-de-history-list-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 1em 0;
  padding: 0 2em;
}

.mt-de-history-list-item-skipped {
  border-left: 3px solid var(--secondary-color-2-1);
}

.mt-de-history-list-item-correct {
  border-left: 3px solid var(--primary-color-0);
}

.mt-de-history-list-item-wrong {
  border-left: 3px solid var(--complement-color-0);
}

.mt-de-history-list-item:hover {
  cursor: pointer;
}

.mt-de-history-list-item-icon {
  max-width: 2em;
}

.mt-de-history-list-item-sequence-number {
  margin: 0 0.25em;
}

.mt-de-history-more-button {
  width: 100%;
}

@media (max-width: 840px) {
  .mt-de-history-list {
    max-height: none;
    overflow-y: auto;
  }
}
